body {
  font-family: DM + Sans, sans-serif;
  letter-spacing: 0.04em;
  background-color: $primary-bg-color;
  color: $primary-text-color;

  @media screen and (min-width: 320px) {
    overflow-x: hidden;
  }
}

p,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
button {
  cursor: pointer;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}
